import React, {Component} from 'react';
import {Navbar, Button} from 'react-bootstrap';
import './App.css';

class App extends Component {
    goTo(route) {
        this.props.history.replace(`/${route}`)
    }

    login() {
        this.props.auth.login();
    }

    logout() {
        this.props.auth.logout();
    }

    render() {
        const {isAuthenticated} = this.props.auth;

        return (
            <div>
                <Navbar inverse className="App-border">
                    <Navbar.Header>
                        <Navbar.Brand>
                            <a href="#">Employee Dashboard</a>
                        </Navbar.Brand>

                        <Button
                            bsStyle="primary"
                            className="btn-margin App-btn-margin"
                            onClick={this.goTo.bind(this, 'home')}
                        >
                            Home
                        </Button>

                        {
                            !isAuthenticated() && (
                                <Button
                                    bsStyle="success"
                                    className="btn-margin App-btn-margin"
                                    onClick={this.login.bind(this)}
                                >
                                    Get Started
                                </Button>
                            )
                        }
                        {
                            isAuthenticated() && (
                                <Button
                                    bsStyle="info"
                                    className="btn-margin App-btn-margin"
                                    onClick={this.goTo.bind(this, 'profile')}
                                >
                                    Profile
                                </Button>
                            )
                        }
                        {
                            isAuthenticated() && (
                                <Button
                                    bsStyle="warning"
                                    className="btn-margin App-btn-margin"
                                    onClick={this.goTo.bind(this, 'details')}
                                >
                                    Details
                                </Button>
                            )
                        }
                        {
                            isAuthenticated() && (
                                <Button
                                    bsStyle="danger"
                                    className="btn-margin App-btn-margin"
                                    onClick={this.logout.bind(this)}
                                >
                                    Log Out
                                </Button>
                            )
                        }
                    </Navbar.Header>
                </Navbar>
            </div>
        );
    }
}

export default App;



